.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #819acc;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(245, 173, 173);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.btn {
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  font-family: Arial;
  color: #eee4ee;
  font-size: 15px;
  background: #62cef8;
  padding: 7px 14px 7px 18px;
  border: solid #8045e0 2px;
  text-decoration: none;
}

.btn:hover {
  background: #f06666;
  text-decoration: none;
}

div.outer{
  background-color: rgb(99, 237, 241);
  border: 2px solid rgb(4, 20, 18);
  border-radius: 5px;
}